// =============================================================================
// GET /api/health - Comprehensive Health Check
// =============================================================================
// Returns health status of all critical services
// Used by load balancers, monitoring systems, and deployment checks

import { NextRequest, NextResponse } from 'next/server';
import { getDb, getPoolStats } from '@/db/client';
import { getAllCircuitBreakerStats } from '@/lib/circuit-breaker';
import { getSecurityMetrics, getSecurityConfig } from '@/lib/security-monitor';
import { createLogger } from '@/lib/logger';

const logger = createLogger({ component: 'HealthCheck' });

// =============================================================================
// TYPES
// =============================================================================

type HealthStatus = 'healthy' | 'degraded' | 'unhealthy';

interface ServiceHealth {
  status: HealthStatus;
  latencyMs?: number;
  message?: string;
  details?: Record<string, any>;
}

interface HealthResponse {
  status: HealthStatus;
  timestamp: string;
  version: string;
  environment: string;
  uptime: number;
  services: Record<string, ServiceHealth>;
  circuitBreakers?: Record<string, any>;
  memory?: {
    heapUsed: number;
    heapTotal: number;
    rss: number;
  };
}

// =============================================================================
// HEALTH CHECKS
// =============================================================================

async function checkDatabase(): Promise<ServiceHealth> {
  const start = Date.now();
  try {
    const db = getDb();
    const healthy = await db.healthCheck();
    const latency = Date.now() - start;
    
    const poolStats = getPoolStats();
    
    if (!healthy) {
      return {
        status: 'unhealthy',
        latencyMs: latency,
        message: 'Database health check failed',
      };
    }
    
    // Warn if latency is high
    if (latency > 500) {
      return {
        status: 'degraded',
        latencyMs: latency,
        message: 'Database responding slowly',
        details: poolStats ? { pool: poolStats } : undefined,
      };
    }
    
    return {
      status: 'healthy',
      latencyMs: latency,
      details: poolStats ? { pool: poolStats } : undefined,
    };
  } catch (error) {
    return {
      status: 'unhealthy',
      latencyMs: Date.now() - start,
      message: error instanceof Error ? error.message : 'Database connection failed',
    };
  }
}

async function checkRedis(): Promise<ServiceHealth> {
  const start = Date.now();
  try {
    // Check if Upstash Redis is configured
    const hasUpstash = !!(process.env.UPSTASH_REDIS_REST_URL && process.env.UPSTASH_REDIS_REST_TOKEN);
    const hasRedis = !!process.env.REDIS_URL;
    
    if (!hasUpstash && !hasRedis) {
      return {
        status: 'healthy',
        message: 'Using in-memory store (no Redis configured)',
        details: { type: 'in-memory' },
      };
    }
    
    // If Upstash is configured, try a simple operation
    if (hasUpstash) {
      const { Redis } = await import('@upstash/redis');
      const redis = new Redis({
        url: process.env.UPSTASH_REDIS_REST_URL!,
        token: process.env.UPSTASH_REDIS_REST_TOKEN!,
      });
      
      await redis.ping();
      const latency = Date.now() - start;
      
      return {
        status: latency > 200 ? 'degraded' : 'healthy',
        latencyMs: latency,
        details: { type: 'upstash' },
        message: latency > 200 ? 'Redis responding slowly' : undefined,
      };
    }
    
    return {
      status: 'healthy',
      message: 'Redis configured but not checked',
      details: { type: 'ioredis' },
    };
  } catch (error) {
    return {
      status: 'degraded', // Not unhealthy since we can fallback to in-memory
      latencyMs: Date.now() - start,
      message: error instanceof Error ? error.message : 'Redis check failed',
      details: { fallback: 'in-memory' },
    };
  }
}

function checkCircuitBreakers(): Record<string, any> {
  const stats = getAllCircuitBreakerStats();
  const result: Record<string, any> = {};
  
  for (const stat of stats) {
    result[stat.name] = {
      state: stat.state,
      failures: stat.failures,
      successRate: Math.round(stat.successRate * 100) + '%',
    };
  }
  
  return result;
}

function checkSecurity(): ServiceHealth {
  try {
    const metrics = getSecurityMetrics();
    const config = getSecurityConfig();
    
    return {
      status: 'healthy',
      details: {
        shadowMode: config.shadowMode,
        totalScanned: metrics.shadowModeStats.totalScanned,
        wouldHaveBlocked: metrics.shadowModeStats.wouldHaveBlocked,
        ipsRateLimited: metrics.violationRateLimiting.ipsRateLimited,
      },
    };
  } catch (error) {
    return {
      status: 'degraded',
      message: 'Security metrics unavailable',
    };
  }
}

// =============================================================================
// ROUTE HANDLER
// =============================================================================

export async function GET(request: NextRequest) {
  const start = Date.now();
  
  // Run health checks in parallel
  const [dbHealth, redisHealth] = await Promise.all([
    checkDatabase(),
    checkRedis(),
  ]);
  
  const securityHealth = checkSecurity();
  const circuitBreakers = checkCircuitBreakers();
  
  // Determine overall status
  const services: Record<string, ServiceHealth> = {
    database: dbHealth,
    redis: redisHealth,
    security: securityHealth,
  };
  
  let overallStatus: HealthStatus = 'healthy';
  
  for (const service of Object.values(services)) {
    if (service.status === 'unhealthy') {
      overallStatus = 'unhealthy';
      break;
    }
    if (service.status === 'degraded' && overallStatus === 'healthy') {
      overallStatus = 'degraded';
    }
  }
  
  // Check if any circuit breaker is open
  const hasOpenCircuit = Object.values(circuitBreakers).some(
    (cb: any) => cb.state === 'OPEN'
  );
  if (hasOpenCircuit && overallStatus === 'healthy') {
    overallStatus = 'degraded';
  }
  
  const response: HealthResponse = {
    status: overallStatus,
    timestamp: new Date().toISOString(),
    version: process.env.npm_package_version || '0.0.0',
    environment: process.env.NODE_ENV || 'development',
    uptime: Math.round(process.uptime()),
    services,
    circuitBreakers: Object.keys(circuitBreakers).length > 0 ? circuitBreakers : undefined,
    memory: {
      heapUsed: Math.round(process.memoryUsage().heapUsed / 1024 / 1024),
      heapTotal: Math.round(process.memoryUsage().heapTotal / 1024 / 1024),
      rss: Math.round(process.memoryUsage().rss / 1024 / 1024),
    },
  };
  
  const latency = Date.now() - start;
  
  logger.info({
    status: overallStatus,
    latencyMs: latency,
    services: Object.fromEntries(
      Object.entries(services).map(([k, v]) => [k, v.status])
    ),
  }, 'Health check completed');
  
  // Return appropriate status code
  const statusCode = overallStatus === 'unhealthy' ? 503 : 200;
  
  return NextResponse.json(response, {
    status: statusCode,
    headers: {
      'Cache-Control': 'no-cache, no-store, must-revalidate',
      'X-Health-Check-Latency': `${latency}ms`,
    },
  });
}

// Also support HEAD for simple availability checks
export async function HEAD() {
  try {
    const db = getDb();
    const healthy = await db.healthCheck();
    return new NextResponse(null, { status: healthy ? 200 : 503 });
  } catch {
    return new NextResponse(null, { status: 503 });
  }
}

