// =============================================================================
// CIRCUIT BREAKER
// =============================================================================
// Protects against cascading failures from external services.
// Implements the circuit breaker pattern with three states:
// - CLOSED: Normal operation, requests pass through
// - OPEN: Failure threshold exceeded, requests fail fast
// - HALF_OPEN: Testing if service recovered

import { createLogger } from './logger';

const logger = createLogger({ component: 'CircuitBreaker' });

// =============================================================================
// TYPES
// =============================================================================

export type CircuitState = 'CLOSED' | 'OPEN' | 'HALF_OPEN';

export interface CircuitBreakerConfig {
  /** Name for logging and metrics */
  name: string;
  
  /** Number of failures before opening circuit */
  failureThreshold: number;
  
  /** Number of successes in HALF_OPEN to close circuit */
  successThreshold: number;
  
  /** Time in ms to wait before trying HALF_OPEN */
  resetTimeout: number;
  
  /** Time window in ms to count failures */
  failureWindow: number;
  
  /** Timeout for individual requests in ms */
  requestTimeout?: number;
  
  /** Optional fallback function when circuit is open */
  fallback?: <T>() => T | Promise<T>;
}

export interface CircuitBreakerStats {
  name: string;
  state: CircuitState;
  failures: number;
  successes: number;
  lastFailure?: Date;
  lastSuccess?: Date;
  openedAt?: Date;
  totalRequests: number;
  failedRequests: number;
  successRate: number;
}

// =============================================================================
// CIRCUIT BREAKER IMPLEMENTATION
// =============================================================================

export class CircuitBreaker {
  private state: CircuitState = 'CLOSED';
  private failures: { timestamp: number }[] = [];
  private successes = 0;
  private lastFailure?: Date;
  private lastSuccess?: Date;
  private openedAt?: Date;
  private totalRequests = 0;
  private failedRequests = 0;

  constructor(private config: CircuitBreakerConfig) {}

  /**
   * Execute a function with circuit breaker protection
   */
  async execute<T>(fn: () => Promise<T>): Promise<T> {
    this.totalRequests++;

    // Check if circuit is open
    if (this.state === 'OPEN') {
      if (this.shouldAttemptReset()) {
        this.transitionTo('HALF_OPEN');
      } else {
        this.failedRequests++;
        logger.warn({
          circuit: this.config.name,
          state: this.state,
          openedAt: this.openedAt,
        }, 'Circuit breaker is OPEN - failing fast');

        if (this.config.fallback) {
          return this.config.fallback<T>();
        }
        throw new CircuitBreakerError(
          `Circuit breaker ${this.config.name} is OPEN`,
          this.config.name
        );
      }
    }

    // Execute the function
    try {
      const result = await this.executeWithTimeout(fn);
      this.onSuccess();
      return result;
    } catch (error) {
      this.onFailure(error);
      throw error;
    }
  }

  /**
   * Execute function with optional timeout
   */
  private async executeWithTimeout<T>(fn: () => Promise<T>): Promise<T> {
    if (!this.config.requestTimeout) {
      return fn();
    }

    return Promise.race([
      fn(),
      new Promise<never>((_, reject) =>
        setTimeout(
          () => reject(new Error(`Request timeout after ${this.config.requestTimeout}ms`)),
          this.config.requestTimeout
        )
      ),
    ]);
  }

  /**
   * Handle successful execution
   */
  private onSuccess(): void {
    this.lastSuccess = new Date();

    if (this.state === 'HALF_OPEN') {
      this.successes++;
      if (this.successes >= this.config.successThreshold) {
        this.transitionTo('CLOSED');
      }
    } else if (this.state === 'CLOSED') {
      // Clear old failures outside the window
      this.pruneOldFailures();
    }
  }

  /**
   * Handle failed execution
   */
  private onFailure(error: unknown): void {
    this.failedRequests++;
    this.lastFailure = new Date();
    this.failures.push({ timestamp: Date.now() });

    logger.warn({
      circuit: this.config.name,
      state: this.state,
      error: error instanceof Error ? error.message : 'Unknown error',
      failureCount: this.failures.length,
    }, 'Circuit breaker recorded failure');

    if (this.state === 'HALF_OPEN') {
      // Any failure in HALF_OPEN reopens the circuit
      this.transitionTo('OPEN');
    } else if (this.state === 'CLOSED') {
      this.pruneOldFailures();
      if (this.failures.length >= this.config.failureThreshold) {
        this.transitionTo('OPEN');
      }
    }
  }

  /**
   * Check if we should attempt to reset from OPEN to HALF_OPEN
   */
  private shouldAttemptReset(): boolean {
    if (!this.openedAt) return true;
    return Date.now() - this.openedAt.getTime() >= this.config.resetTimeout;
  }

  /**
   * Remove failures outside the failure window
   */
  private pruneOldFailures(): void {
    const cutoff = Date.now() - this.config.failureWindow;
    this.failures = this.failures.filter(f => f.timestamp > cutoff);
  }

  /**
   * Transition to a new state
   */
  private transitionTo(newState: CircuitState): void {
    const oldState = this.state;
    this.state = newState;

    logger.info({
      circuit: this.config.name,
      from: oldState,
      to: newState,
      failures: this.failures.length,
      successes: this.successes,
    }, 'Circuit breaker state transition');

    if (newState === 'OPEN') {
      this.openedAt = new Date();
    } else if (newState === 'CLOSED') {
      this.failures = [];
      this.successes = 0;
      this.openedAt = undefined;
    } else if (newState === 'HALF_OPEN') {
      this.successes = 0;
    }
  }

  /**
   * Get current statistics
   */
  getStats(): CircuitBreakerStats {
    return {
      name: this.config.name,
      state: this.state,
      failures: this.failures.length,
      successes: this.successes,
      lastFailure: this.lastFailure,
      lastSuccess: this.lastSuccess,
      openedAt: this.openedAt,
      totalRequests: this.totalRequests,
      failedRequests: this.failedRequests,
      successRate: this.totalRequests > 0
        ? (this.totalRequests - this.failedRequests) / this.totalRequests
        : 1,
    };
  }

  /**
   * Manually reset the circuit breaker
   */
  reset(): void {
    this.transitionTo('CLOSED');
    this.totalRequests = 0;
    this.failedRequests = 0;
    logger.info({ circuit: this.config.name }, 'Circuit breaker manually reset');
  }

  /**
   * Check if circuit is allowing requests
   */
  isAllowingRequests(): boolean {
    if (this.state === 'CLOSED') return true;
    if (this.state === 'HALF_OPEN') return true;
    return this.shouldAttemptReset();
  }
}

// =============================================================================
// CIRCUIT BREAKER ERROR
// =============================================================================

export class CircuitBreakerError extends Error {
  constructor(
    message: string,
    public readonly circuitName: string
  ) {
    super(message);
    this.name = 'CircuitBreakerError';
  }
}

// =============================================================================
// CIRCUIT BREAKER REGISTRY
// =============================================================================

const circuitBreakers = new Map<string, CircuitBreaker>();

/**
 * Get or create a circuit breaker
 */
export function getCircuitBreaker(config: CircuitBreakerConfig): CircuitBreaker {
  let breaker = circuitBreakers.get(config.name);
  if (!breaker) {
    breaker = new CircuitBreaker(config);
    circuitBreakers.set(config.name, breaker);
    logger.info({ circuit: config.name }, 'Created new circuit breaker');
  }
  return breaker;
}

/**
 * Get all circuit breaker stats
 */
export function getAllCircuitBreakerStats(): CircuitBreakerStats[] {
  return Array.from(circuitBreakers.values()).map(cb => cb.getStats());
}

/**
 * Reset all circuit breakers
 */
export function resetAllCircuitBreakers(): void {
  circuitBreakers.forEach(cb => cb.reset());
  logger.info({ count: circuitBreakers.size }, 'Reset all circuit breakers');
}

// =============================================================================
// PRE-CONFIGURED CIRCUIT BREAKERS
// =============================================================================

/**
 * Circuit breaker for x402scan API
 */
export const x402scanCircuitBreaker = getCircuitBreaker({
  name: 'x402scan',
  failureThreshold: 3,
  successThreshold: 2,
  resetTimeout: 60000, // 1 minute
  failureWindow: 300000, // 5 minutes
  requestTimeout: 30000, // 30 seconds
});

/**
 * Circuit breaker for Scattering/Dune API
 */
export const scatteringCircuitBreaker = getCircuitBreaker({
  name: 'scattering',
  failureThreshold: 3,
  successThreshold: 2,
  resetTimeout: 60000,
  failureWindow: 300000,
  requestTimeout: 30000,
});

/**
 * Circuit breaker for facilitator health probes
 */
export const facilitatorProbeCircuitBreaker = getCircuitBreaker({
  name: 'facilitator-probes',
  failureThreshold: 5,
  successThreshold: 3,
  resetTimeout: 30000, // 30 seconds (more aggressive retry)
  failureWindow: 120000, // 2 minutes
  requestTimeout: 10000, // 10 seconds
});

